<template>
<html>
  <body>
  <v-app>
    <div id="app">
      <div class="text-center">
    <v-app-bar elevation="12"
    color="primary"> <v-toolbar-title class="mx-auto white--text text-center">Fuel App</v-toolbar-title></v-app-bar>
    <br>
    <br>
    <v-alert
      dense
      type="info"
      max-width="300px"
      class="mx-auto"
      elevation="20"
    >Petrol Price is Rs.{{main[0].petrolPrice}}/Litre as of {{main[0].priceDate.slice(0, 10)}}</v-alert>
    <v-card class="mx-auto pa-4" elevation="24" max-width="450">
      <br>
      <v-row no-gutters> 
      <v-col cols="4" class="c1">Vehicle Name:</v-col><v-col><v-text-field @click="flag=false" v-model="vname" dense rounded class="mx-5" label="vehicle Name" outlined></v-text-field></v-col>
      </v-row>
      <v-row no-gutters> 
      <v-col cols="4" class="c1">Mileage(kmpl):</v-col><v-col><v-text-field @click="flag=false" v-model="mil" dense rounded class="mx-5" label="Mileage" outlined></v-text-field></v-col>
      </v-row>
      <v-row no-gutters> 
      <v-col cols="4" class="c1">Distance(km):</v-col><v-col><v-text-field @click="flag=false" v-model="dist" dense rounded class="mx-5" label="Distance" outlined></v-text-field></v-col>
      </v-row>
      <h3>The Fuel Price for {{vname}} is: Rs.{{result}}</h3>
      <br>
      <v-row no-gutters> 
      <v-col align="center"><v-btn elevation="12" rounded @click="clear" color="primary">Clear</v-btn></v-col>
      <v-col align="start" class="mr-5"><v-btn elevation="12" rounded @click="add" color="primary">Add for Comparison</v-btn></v-col>
      </v-row>
      <br>
      <v-alert
      border="left"
      color="indigo"
      dark
      v-show=flag
      elevation="7"
    >
      {{msg}}
    </v-alert>
    </v-card>
    <v-row class="mt-2 pa-2" v-for="item in li" :key="item.Vehicle">
      <v-card class="mx-auto" color="white" elevation="14" max-width="450">
      <v-card-text>
      <v-card-title><b>Vehicle Name: {{item.Vehicle}}</b></v-card-title>
      <b><v-chip color="primary" class="elevation-10">Mileage: {{item.Mileage}} Kmpl
        </v-chip><br>
        <br>
      <v-chip color="primary"  class="elevation-10"><v-avatar left><v-icon>mdi-cash-multiple</v-icon></v-avatar> price:Rs. {{item.Price}}</v-chip></b> 
      </v-card-text>
      </v-card>
    </v-row>
      </div>
    </div>
  </v-app>
  </body>
</html>
</template>

<script>
import axios from 'axios';
export default {
  name: 'App',

  components: {
  },

  data: () => {
    //result:null
    return {
      vname:"",
      mil:"",
      dist:"",
      li:[],
      flag:false,
      msg:"",
      main:{},
      finalval:null
      }
    },
  computed:
  {
    result()
    {
      if(this.mil){
          return ((this.dist/this.mil)*this.main[0].petrolPrice).toFixed(2);
      }
      else
      {
        return 0;
      }
    }
  },
  created(){
     axios.get("https://mfapps.indiatimes.com/ET_Calculators/oilprice.htm?citystate=tamil-nadu")
    .then(response => this.main = response.data.results);
    this.finalval=this.main.petrolPrice
  },
  methods:
  {
    clear(){
      this.vname="";
      this.mil="";
      this.dist=""; 
    },
    add()
    {
      if(!this.vname)
      {
        this.flag=true;
        this.msg="Please Enter a Name";
      }
      if(parseInt(this.mil)<0 || parseInt(this.dist)<0)
      {
        this.flag=true;
        this.msg="Please enter a Positive value";
      }
      for(var i=0;i<this.li.length;i++){
        if(this.li[i].Vehicle===this.vname){
          this.flag=true;
          this.msg="This vehicle already exists"
        break;
        } 
      }
      if(this.flag==false)
      {
      this.li.push({"Vehicle":this.vname,"Mileage":this.mil,"Distance":this.dist,"Price":this.result});
      }
    }
  }
};
</script>
